import {URL} from "../../url/url";
import axios from 'axios'
export const getAdminList = async (api) => {
    return await axios.get(`${URL}/${api}`);
}

export const deleteAdmin = async (id) => {
    let api = 'api/delete-admin';
    return await axios.post(`${URL}/${api}`,id);
}
export const addAdmin = async (id) => {
    let api = 'api/add-admin';
    return await axios.post(`${URL}/${api}`,id);
}
export const getAdminDetail = async (id) => {
    let api = 'api/admin-details';
    return await axios.post(`${URL}/${api}/${id}`);
}

export const updateAdmin = async (id,request) => {
    let api = 'api/update-details';
    return await axios.post(`${URL}/${api}/${id}`,request);
}
